import React from 'react';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Title from './Title';

// Generate Order Data
function createData(id, dateFist, order, token, priceFirst, dateFinal, priceFinal) {
  return { id, dateFist, order, token, priceFirst, dateFinal, priceFinal };
}

const rows = [
  createData(0, '16 Mar, 2019', 'BUY', 'BTC', '30000$', '18 Mar, 2019', '31000$'),
  createData(1, '18 Mar, 2019', 'SELL', 'BTC', '31200$', '20 Mar, 2019', '30000$'),
  createData(2, '20 Mar, 2019', 'SELL', 'BTC', '31200$', '21 Mar, 2019', '33000$'),
  createData(3, '22 Mar, 2019', 'BUY', 'BTC', '31200$', '26 Mar, 2019', '35000$'),
  createData(4, '26 Mar, 2019', 'SELL', 'BTC', '31200$', '30 Mar, 2019', '35000$')
];

function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

export default function Orders() {
  const classes = useStyles();
  return (
    <React.Fragment>
      <Title>Últimas Trades</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Data</TableCell>
            <TableCell>Tipo de Ordem</TableCell>
            <TableCell>Activo</TableCell>
            <TableCell>Preço Inicial</TableCell>
            <TableCell>Data Final</TableCell>
            <TableCell>Preço Final</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.dateFist}</TableCell>
              <TableCell>{row.order}</TableCell>
              <TableCell>{row.token}</TableCell>
              <TableCell>{row.priceFirst}</TableCell>
              <TableCell>{row.dateFinal}</TableCell>
              <TableCell>{row.priceFinal}</TableCell>
              <TableCell align="right">{row.amount}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <div className={classes.seeMore}>
        <Link color="primary" href="#" onClick={preventDefault}>
          See more orders
        </Link>
      </div>
    </React.Fragment>
  );
}
